:root{
    --dark:#141212;
    --warning: #f6981e;
    --white: #fff;
}

.Divider{
    display: grid;
    
    grid-template-columns: 1fr 1fr;
    width: 100%;
    height: 100vh;
}

.ContactContent{
    background-color: var(--dark);
    color: var(--white);
    text-align: center;
    padding: 15% 5%;
}

.ContactContent li{
    list-style: none;
}

